/**
 * @class Completor
 */
import ColorHash from "color-hash";
import * as React from "react";

import { filterCompletions, removeStopWords, getModifiedInput } from "./utils";
import styles from "./styles.css";

type Chunk = { type: string; value: string };
type Data = {
  sentences: string[];
  chunks: Chunk[];
};

type PopupDirection = "bottom" | "top";
export type Props = {
  id?: string;
  placeholder: string;
  data: Data;
  trigger(): React.ReactNode;
  onSubmit(input: string): void;
  style: any; // css styles
  startAt?: number;
  popupDirection?: PopupDirection;
  innerStyle: { [key: string]: React.CSSProperties };
  remoteCompletionUrl?: string;
};

type State = {
  open: boolean;
  input: string;
  sentences: { sentence: string; cleanedSentence: string }[];
  chunks: Chunk[];

  filterdChunks: Chunk[];
  filterdSentences: { sentence: string }[];

  controller?: AbortController;
};

let completionResponses = {};

export default class Completor extends React.Component<Props, State> {
  private inputNode: HTMLInputElement | null;

  constructor(props: Props) {
    super(props);

    let sentences = props.data.sentences.map(sentence => {
      return { sentence, cleanedSentence: removeStopWords(sentence).trim() };
    });

    this.state = {
      open: false,
      input: "",
      sentences,
      chunks: props.data.chunks,
      filterdChunks: [],
      filterdSentences: [],
      controller: undefined
    };
  }

  static getDerivedStateFromProps(props: Props, state: State) {
    if (
      state.chunks.length === props.data.chunks.length &&
      state.sentences.length === props.data.sentences.length
    )
      return null;

    let sentences = props.data.sentences.map(sentence => {
      return { sentence, cleanedSentence: removeStopWords(sentence).trim() };
    });

    completionResponses = {};

    return {
      open: state.open ? state.open : false,
      input: state.input ? state.input : "",
      sentences,
      chunks: props.data.chunks
    };
  }

  completionChosen = (e: any, type: string, data: any): string => {
    if (e) {
      e.stopPropagation();
      if (e.nativeEvent) e.nativeEvent.stopImmediatePropagation();
    }
    if (this.inputNode) this.inputNode.focus();

    let { input } = this.state;
    const modifiedInput = getModifiedInput(input, type, data);
    this.setState({
      ...this.state,
      input: modifiedInput
    });

    this.onInputChange(modifiedInput)  // reload completion after choosing completion
    return modifiedInput;
  };

  getCompletions = (
    _input: string,
    _sentences: { sentence: string; cleanedSentence: string }[],
    _chunks: Chunk[],
    _popupDirection: PopupDirection
  ) => {
    let { startAt = 3 } = this.props;
    if (startAt < 1) {
      console.warn(
        "react-completor: startAt has to be greater than 0. Using 1 instead"
      );
      startAt = 1;
    }
    let { filterdChunks, filterdSentences, sentences = [] } = this.state;

    // Make sure there is something to display always
    if (filterdSentences.length === 0) {
      //@ts-ignore
      filterdSentences = sentences.map(s => s.sentence);
    }

    const colorHash = new ColorHash({ lightness: 0.5 });
    const sentenceCompletions = (
      <div key="sentence-completions" className={styles.sentence_completions}>
        {filterdSentences.map((sentence, key) => {
          return (
            <div
              key={key}
              className={styles.sentence_completion}
              onClick={e => this.completionChosen(e, "sentence", sentence)}
            >
              {sentence}
            </div>
          );
        })}
      </div>
    );
    const chunkCompletions = (
      <div
        key="chunk-completions"
        className={styles.chunk_completions}
        style={{ height: filterdChunks.length === 0 ? "0" : "auto" }}
      >
        {filterdChunks.map((chunk, key) => {
          const color = colorHash.rgb(chunk.type);
          return (
            <div
              key={key + chunk.type + "_" + chunk.value}
              className={styles.chunk_completion}
              style={{
                borderColor: `rgba(${color[0]}, ${color[1]}, ${color[2]}, 0.6)`
              }}
              onClick={e => this.completionChosen(e, "chunk", chunk)}
            >
              <div className={styles.chunk_completion_type}>{chunk.type}</div>
              <div className={styles.chunk_completion_value}>{chunk.value}</div>
            </div>
          );
        })}
      </div>
    );
    const noCompletions = (
      <div className={styles.no_completions}>No completions available</div>
    );
    if (filterdSentences.length + filterdChunks.length === 0)
      return noCompletions;

    return [sentenceCompletions, chunkCompletions];
    // if (popupDirection === "top")
    //   return [sentenceCompletions, chunkCompletions];
    // else return [chunkCompletions, sentenceCompletions];
  };

  onInputChange = (event: React.FormEvent<HTMLInputElement> | string) => {
    let input;
    if (typeof event === "string") {
      input = event;
    } else {
      input = event.currentTarget.value;
    }

    const { controller } = this.state;
    if (controller && controller !== undefined) controller.abort();

    let newController;
    if (input.trim() in completionResponses) {
      const { filterdChunks, filterdSentences } = completionResponses[
        input.trim()
      ];
      this.setState({
        ...this.state,
        input,
        filterdChunks,
        filterdSentences,
        controller: undefined
      });
    } else {
      newController = this.updateFilteredCompletions(input.trim());

      this.setState({
        ...this.state,
        input,
        controller: newController
      });
    }
  };

  updateFilteredCompletions(input: string) {
    let { sentences, chunks } = this.state;
    let { startAt, remoteCompletionUrl } = this.props;
    if (!startAt || startAt < 1) startAt = 1;

    const { fetchPromise, controller } = filterCompletions(
      input,
      sentences,
      chunks,
      startAt,
      remoteCompletionUrl
    );

    fetchPromise.then(filterdData => {
      let [filterdSentences, filterdChunks] = filterdData;
      // cache the response
      completionResponses[input] = { filterdChunks, filterdSentences };
      if (input.trim() === this.state.input.trim()) {
        // if input has not changed from the one requested
        // @ts-ignore
        this.setState(state => {
          return {
            ...state,
            filterdChunks,
            filterdSentences
          };
        });
      }
    });

    return controller;
  }

  componentDidMount() {
    document.addEventListener("mousedown", this.toggleClose, false);
    this.updateFilteredCompletions("");
  }

  componentWillUnmount() {
    document.removeEventListener("mousedown", this.toggleClose, false);
  }

  toggleClose = (e: any) => {
    if (e) {
      e.stopPropagation();
      if (e.nativeEvent) e.nativeEvent.stopImmediatePropagation();
    }
    this.setState({
      ...this.state,
      open: false
    });
  };

  toggleOpen = (e: any) => {
    if (e) {
      e.stopPropagation();
      if (e.nativeEvent) e.nativeEvent.stopImmediatePropagation();
    }
    this.setState({
      ...this.state,
      open: true
    });
  };

  onSubmit() {
    const { onSubmit } = this.props;
    const { input } = this.state;
    completionResponses = {}; // reset cache data
    if (input.trim().length > 0) {
      onSubmit(input);
      this.setState({ ...this.state, input: "", open: false });
      if (this.inputNode) this.inputNode.focus();
    }
  }

  onButtonClick = (e: any) => {
    if (e) {
      e.stopPropagation();
      if (e.nativeEvent) e.nativeEvent.stopImmediatePropagation();
    }
    this.onSubmit();
  };

  onKeyDown = (e: any) => {
    if (e.keyCode === 13) {
      this.onSubmit();
    } else if (e.keyCode === 9) {
      e.preventDefault();

      const { filterdChunks, filterdSentences } = this.state;
      if (filterdChunks.length > 0) {
        const newText = this.completionChosen(
          undefined,
          "chunk",
          filterdChunks[0]
        );
        this.onInputChange(newText);
      } else if (filterdSentences.length > 0) {
        const newText = this.completionChosen(
          undefined,
          "sentence",
          filterdSentences[0]
        );
        this.onInputChange(newText);
      }
    } else {
      this.toggleOpen(e);
    }
  };

  render() {
    const {
      placeholder,
      trigger,
      style,
      id = "react-completor-user-input",
      popupDirection = "top",
      innerStyle = {}
    } = this.props;
    const { input, sentences, chunks, open } = this.state;

    const wrapperClassName = styles.wrapper + " " + (open ? styles.open : "");
    const {
      input: inputStyle = {},
      form: formStyle = {},
      button: buttonStyle = {},
      completions: completionsStyle = {}
    } = innerStyle;

    return (
      <div
        style={style}
        className={wrapperClassName}
        onMouseDown={this.toggleOpen}
      >
        {popupDirection === "top" && (
          <div className={styles.completions} style={completionsStyle}>
            {this.getCompletions(input, sentences, chunks, popupDirection)}
          </div>
        )}
        {/* Use form here? */}
        <div
          className={styles.interactions + " " + styles.ripple}
          style={formStyle}
        >
          <input
            id={id}
            style={inputStyle}
            ref={node => (this.inputNode = node)}
            onKeyDown={this.onKeyDown}
            className={styles.input}
            type="text"
            autoComplete="off"
            autoFocus={true}
            placeholder={placeholder}
            onChange={this.onInputChange}
            value={input}
          />
          <button
            className={styles.trigger}
            onClick={this.onButtonClick}
            style={buttonStyle}
          >
            {trigger()}
          </button>
        </div>

        {popupDirection === "bottom" && (
          <div className={styles.completions} style={completionsStyle}>
            {this.getCompletions(input, sentences, chunks, popupDirection)}
          </div>
        )}
      </div>
    );
  }
}
