import { stopWords } from "./stopwords";
// import AbortController from "abort-controller";

type Chunk = { type: string; value: string };
export type SentenceMatch = { sentence: string; wordCount: number };
export type ChunkMatch = {
  value: string;
  type: string;
  wordCount: number;
  value_match: boolean;
};

export const filterCompletions = (
  user_input: string,
  sentences: { sentence: string; cleanedSentence: string }[],
  chunks: Chunk[],
  startAt: number,
  remoteUrl?: string
): {
  fetchPromise: Promise<[string[], Chunk[]]>;
  controller?: AbortController;
} => {
  if (remoteUrl) return filterCompletionsRemote(user_input, startAt, remoteUrl);
  return filterCompletionsLocal(user_input, sentences, chunks, startAt);
};

const filterCompletionsRemote = (
  user_input: string,
  startAt: number,
  remoteUrl: string
): {
  fetchPromise: Promise<[string[], Chunk[]]>;
  controller?: AbortController;
} => {
  const controller = new AbortController();
  const fetchPromise = new Promise<[string[], Chunk[]]>((accept, _reject) => {
    fetch(remoteUrl, {
      method: "post",
      signal: controller.signal,
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      },
      body: JSON.stringify({ sentence: user_input, startAt })
    })
      .then(resp => {
        return resp.json();
      })
      .then(resp => {
        let { filteredSentences, filteredChunks } = resp.completions;
        accept([filteredSentences, filteredChunks]);
      })
      .catch(e => {
        if (e.name !== "AbortError") console.warn(e); // don't worry, just aborting the fetch
      });
  });
  return { fetchPromise, controller };
};

export const filterCompletionsLocal = (
  user_input: string,
  sentences: { sentence: string; cleanedSentence: string }[],
  chunks: Chunk[],
  startAt: number
): { fetchPromise: Promise<[string[], Chunk[]]>; controller: any } => {
  const fetchPromise = new Promise<[string[], Chunk[]]>(accept => {
    user_input = user_input.trim();
    let input = removeStopWords(user_input.toLowerCase());
    if (input.length < startAt)
      accept([sentences.map(sentence => sentence.sentence), []]);

    const words = input.split(" ");
    const lastWord = user_input.split(" ")[user_input.split(" ").length - 1];

    let sentenceMatches: SentenceMatch[] = [];
    sentences.forEach(sentence => {
      let lsc = 0;
      for (let word of words) {
        if (word.length < startAt) continue;
        if (sentence.cleanedSentence.includes(word)) lsc += 1;
      }
      if (lsc > 0)
        sentenceMatches.push({ sentence: sentence.sentence, wordCount: lsc });
    });

    sentenceMatches = sentenceMatches.sort(
      (s1, s2) => s2.wordCount - s1.wordCount
    );

    let chunkMatches: ChunkMatch[] = [];
    const max_words = words.length;

    if (!stopWords.includes(lastWord))
      // faster than using the whole sentence
      for (let i = 0; i < max_words; i++) {
        let sub = [...words]
          .splice(i)
          .join(" ")
          .toLowerCase();
        if (sub.length < startAt) continue;

        chunks.forEach(chunk => {
          if (
            chunkMatches.length < 50 &&
            (chunk.type.toLowerCase() === sub ||
              chunk.value.toLowerCase().startsWith(sub))
          )
            chunkMatches.push({
              ...chunk,
              wordCount: words.length - i,
              value_match: chunk.type.toLowerCase() === sub
            });
        });
      }

    if (sentenceMatches.length === 0)
      accept([sentences.map(sentence => sentence.sentence), chunkMatches]);

    accept([sentenceMatches.map(sentence => sentence.sentence), chunkMatches]);
  });

  return { fetchPromise, controller: undefined };
};

export const removeStopWords = (sentence: string): string => {
  sentence = sentence.toLowerCase();
  for (let word of stopWords) {
    sentence = sentence.replace(new RegExp("\\b" + word + "\\b"), " ");
  }
  return sentence;
};

export const getModifiedInput = (
  input: string,
  type: string,
  data: any
): string => {
  if (type === "sentence") input = data;
  else if (type === "chunk") {
    if (data.value_match) input = input.trim() + " " + data.value;
    else {
      let words = input.trim().split(" ");
      input =
        words.slice(0, words.length - data.wordCount).join(" ") +
        " " +
        data.value;
    }
  }
  return input.trim();
};
